import Qrcode from "qrcode";
var Buffer = require("buffer/").Buffer;

// Final Buff Obj
let finalBuf = null;

// CAlcualtion for each value in the Taged list
function getTlvForValue(tagNum, tagValue) {
  let tagNumBuf = Buffer.from([tagNum], "utf8");
  let tagValueLenBuf = Buffer.from([tagValue.length], "utf8");
  let tagValueBuf = Buffer.from(tagValue, "utf8");

  let arrBuff = [tagNumBuf, tagValueLenBuf, tagValueBuf];
  return Buffer.concat(arrBuff);
}

// Concat and get final buf
function setFinalBuf(obj) {
  let tagsBufs = [];
  for (let i = 0; i < Object.keys(obj).length; i++) {
    let key = Object.keys(obj)[i];
    tagsBufs.push(getTlvForValue(String(i + 1), obj[key]));
  }
  finalBuf = Buffer.concat(tagsBufs);
}

// Hex String
export const toHex = function (obj) {
  if (!obj) return null;
  setFinalBuf(obj);
  return finalBuf.toString("hex");
};

// Base64 String
export const toBase64 = function (obj) {
  if (!obj) return null;
  setFinalBuf(obj);
  return finalBuf.toString("base64");
};

//Qr code image as base64
export const toQrCode = async function (obj) {
  if (!obj) return null;
  let qr = await Qrcode.toDataURL(toBase64(obj));
  return new Promise((res, rej) => {
    res(qr);
  });
};

