import React, { useEffect, useState } from "react";
import QRCode from "qrcode";
import { toBase64 } from "./qrmaker";

import "./App.css";

function App() {
  const [companyName, setCompanyName] = useState("");
  const [vatID, setVatID] = useState("");
  const [amount, setAmount] = useState("");
  const [vat, setVAT] = useState(0);
  const [total, setTotal] = useState(0);
  const [QRCodeValue, setCode] = useState("");

  useEffect(() => {
    getDataFromLocalStore();
  }, []);

  const getDataFromLocalStore = () => {
    const storedData = localStorage.getItem("companyData");
    if (storedData) {
      const data = JSON.parse(storedData);
      setCompanyName(data.companyName);
      setVatID(data.vatID);
    }
  };

  const saveDataToLocalStore = (companyName, vatID) => {
    const data = { companyName: companyName, vatID: vatID };
    localStorage.setItem("companyData", JSON.stringify(data));
  };

  const onTextChange = value => {
    setAmount(value);
    const vatAmount = parseFloat(value) * 0.15;
    const totalAmount = parseFloat(value) + vatAmount;
    setVAT(vatAmount.toFixed(2));
    setTotal(totalAmount.toFixed(2));
    setCode("");
  };

  const onCreateQR = () => {
    if (amount && companyName && vatID) {
      const baseObj = {
        seller: companyName,
        vatRegNumber: vatID,
        timeStamp: new Date().toISOString(),
        // timeStamp: new Date("07-07-2023 16:35:32").toISOString(),
        totalAmount: total,
        vatAmount: vat
      };
      QRCode.toDataURL(toBase64(baseObj), function(err, code) {
        if (err) return console.log("error occurred");
        setCode(code);
        saveDataToLocalStore(companyName, vatID);
      });
    }
  };
  return (
    <div style={{ width: "100%", textAlign: "center", marginTop: "1 %" }}>
      <h2 style={{ marginBottom: "50px" }}>Generate QR</h2>
      <input
        type="text"
        name="name"
        placeholder="Company Name"
        style={{ marginBottom: "5px", width: "300px" }}
        value={companyName}
        onChange={e => setCompanyName(e.target.value)}
      />

      <br />
      <input
        type="text"
        name="vatId"
        placeholder="VAT Number"
        style={{ width: "300px" }}
        value={vatID}
        onChange={e => setVatID(e.target.value)}
      />

      <br />
      <input
        type="text"
        name="total"
        placeholder="Total Amount"
        style={{ width: "120px" }}
        value={amount}
        onChange={e => onTextChange(e.target.value)}
      />
      <button
        style={{ marginTop: "20px", marginBottom: "20px" }}
        onClick={() => onCreateQR()}
      >
        Generate QR
      </button>
      <br />
      {amount &&
        companyName &&
        vatID &&
        <div
          style={{
            border: 1,
            backgroundColor: "#f1f1f1",
            width: "30%",
            padding: "10px",
            lineHeight: "2",
            margin: "auto"
          }}
        >
          {/* <div style={{ marginBottom: "20px" }}>
            <b>Company Name:</b> {companyName}
            <br />
            <b>VAT Register Number:</b> {vatID}
          </div> */}
          <b>Amount:</b> {amount ? parseFloat(amount).toFixed(2) : "0.00"}
          <br />
          <b>15% VAT:</b> {vat ? vat : "0.00"}
          <br />
          <b>Total including VAT:</b> {total ? total : "0.00"}
        </div>}
      {amount &&
        QRCodeValue &&
        <div style={{ width: "100%", textAlign: "center", marginTop: "2%" }}>
          <img src={QRCodeValue} alt="QRCOde" />
        </div>}
    </div>
  );
}

export default App;
